<script setup lang="ts">
import { onMounted, ref, watch } from 'vue'
import NewOportunity from './NewOportunity.vue'
import { deleteOportunity, getAllOportunities } from '../../../core/requests/Oportunities'
import { useToast } from 'vuestic-ui/web-components'
import { format } from 'date-fns'
import { newOrder } from '../../../core/requests/Orders'
import { useRouter } from 'vue-router'
import { useOrdersStore } from '../../../stores/orders-store'
import { storeToRefs } from 'pinia'
import { useOportunitiesStore } from '../../../stores/oportunities-store'
import { HOST } from '../../../core/constants'

const { notify } = useToast()
const router = useRouter()
const items = ref([])
const columns = [
  { key: 'id', sortable: false },
  { key: 'Descripción', sortable: false },
  { key: 'Cliente', sortable: false },
  { key: 'Servicios', sortable: false },
  { key: 'Total', sortable: false },
  { key: 'Comercial', sortable: false },
  { key: 'Creación', sortable: false },
  { key: 'Estado', sortable: false },
  { key: 'actions', width: 80 },
]
const orderStore = useOrdersStore()
const { viewOrders, orders } = storeToRefs(orderStore)
const oportunityStore = useOportunitiesStore()
const { statusOptions } = storeToRefs(oportunityStore)

const showConfirm: any = ref(null)
const showConfirmStatus: any = ref(null)
const showEdit: any = ref(null)
const onlyPreview = ref(false)
const showNewOportunity = ref(false)
const isDeleting = ref(false)
const isLoading = ref(false)
const isOnChangeStatus: any = ref(0)
//
const inputSearch = ref('')
const startDate = ref({
  start: null,
  end: null,
})
const filteredCount = ref(0)
const perPage = ref(15)
const currentPage = ref(1)
const pages = () => {
  console.log(items.value.length)
  return perPage.value && perPage.value !== 0 ? Math.ceil(items.value.length / perPage.value) : items.value.length
}
//
const doDelete = async () => {
  await deleteOportunity(showConfirm.value)
  showConfirm.value = null

  notify({
    message: 'Cotización eliminada correctamente',
    color: 'success',
    duration: 1000,
  })

  getData()
}

const getData = async (filter?: any) => {
  isLoading.value = true
  const data: any = await getAllOportunities(filter)
  await oportunityStore.getQuotationStates()

  items.value = data.map((e: any) => {
    const state = { state: { ...e }.status }
    return {
      data: {
        ...e,
        ...state,
      },
      createdAt: format(new Date(e.created_at), 'dd/MM/yyyy hh:mm'),
      Creación: format(new Date(e.created_at), 'dd/MM/yy hh:mm'),
      updatedAt: format(new Date(e.updated_at), 'dd/MM/yyyy hh:mm'),
      id: '00' + e.id,
      Cliente: e.customer.company ? e.customer.socialreason : '' + e.customer.fullname,
      Servicios: e.services.map((e: any) => ({ label: e.service.detail?.service, value: e.service.detail?.id })),
      Total: 'S/ ' + e.total,
      Descripción: e.title,
      Comercial: e.owner?.name,
      Acciones: true,
    }
  })

  isLoading.value = false
}
const openPreview = (item: any) => {
  showEdit.value = item
  onlyPreview.value = true
}
const closeNew = () => {
  getData()
  onlyPreview.value = false
  showNewOportunity.value = false
}
const onChangeStatus = async (data: any) => {
  oportunityStore.setActiveOportunity(data)
  oportunityStore.viewConfirm()
}
const cancelStatus = () => {
  showConfirmStatus.value = null
}
const doChangeStatus = () => {}
onMounted(() => {
  watch(
    () => oportunityStore.vailable,
    () => {
      console.log('updated')
    },
  )
  getData()
})
const openOrder = async (id: any) => {
  isLoading.value = true

  const order: any = await newOrder({
    id,
  })
  const newOrders = order.news
  const exists = order.exists

  if (exists.length > 0) {
    notify({
      message: 'La cotización tiene ordenes existentes',
      color: 'info',
      duration: 2500,
    })
    orderStore.showOrders(exists)
    isLoading.value = false
  } else {
    if (newOrders.length > 1) {
      notify({
        message: 'Proceso realizado correctamente',
        color: 'success',
        duration: 2500,
      })
      orderStore.showOrders(newOrders)
      isLoading.value = false
    } else {
      notify({
        message: 'Se ha creado la orden #000' + newOrders[0] + '-24',
        color: 'success',
        duration: 2500,
      })
      isLoading.value = false
      router.push('/orders/view/' + newOrders[0])
    }
  }
}
const doFilterByRange = () => {
  const { start, end } = startDate.value
  if (start && end) {
    const fromDate = new Date(start).setHours(0)
    const toDate = new Date(end).setHours(24)
    getData({
      start: new Date(fromDate).getTime(),
      end: new Date(toDate).getTime(),
    })
  }
}
const isDownloading = ref(false)
const download = () => {
  isDownloading.value = true
  setTimeout(() => {
    isDownloading.value = false
  }, 2000)
  console.log('download')
}
const isLoadingPDF = ref(null)
const togglePdfLoading = (id: any) => {
  isLoadingPDF.value = id

  setTimeout(() => {
    isLoadingPDF.value = null
  }, 2500)
}
</script>

<template>
  <div class="py-4">
    <div class="flex justify-between align-center justify-center">
      <h1 class="h5 m-0">Cotizaciones ({{ items.length }})</h1>

      <div class="flex gap-2">
        <VaButton
          icon="exit_to_app"
          color="info"
          :loading="isDownloading"
          :href="HOST + '/oportunities/xls'"
          @click="download"
        >
          <span class="px-1">Descargar</span>
        </VaButton>
        <VaButton icon="add" color="primary" @click="showNewOportunity = true">
          <span class="px-1">Nueva cotización</span>
        </VaButton>
      </div>
    </div>

    <div class="columns-5 mb-7 mt-5">
      <div>
        <VaInput
          v-model="inputSearch"
          label="Buscar en resultados"
          placeholder="Escribe lo que buscas"
          clearable
          background="#fff"
        />
      </div>
      <div>
        <VaDateInput
          v-model="startDate"
          label="Filtrar por fecha"
          placeholder="Ingresa una fecha"
          mode="range"
          background="#fff"
          @update:modelValue="doFilterByRange"
        />
      </div>
    </div>

    <div class="flex columns-1 mt-5">
      <div class="w-full bg-white p-2">
        <VaDataTable
          :items="items"
          :columns="columns"
          class="text-xs"
          :filter="inputSearch"
          :per-page="perPage"
          :current-page="currentPage"
          :loading="isLoading"
          :no-data-html="'...'"
          @filtered="filteredCount = $event.items.length"
        >
          <template #cell(actions)="{ row }">
            <div class="flex gap-1">
              <VaButton
                icon="delete"
                color="danger"
                :disabled="row.itemKey.data.status === 4"
                round
                @click="showConfirm = row.itemKey.data.id"
              ></VaButton>
              <VaButton
                icon="download"
                color="#158de3"
                round
                :loading="row.itemKey.data.id == isLoadingPDF"
                :href="HOST + '/oportunities/pdf/' + row.itemKey.data.id"
                @click="togglePdfLoading(row.itemKey.data.id)"
              ></VaButton>
              <VaButton icon="visibility" color="#6c48d9" round @click="openPreview(row.itemKey.data)"></VaButton>
              <VaButton
                v-if="row.itemKey.data.status === 4"
                icon="library_add"
                color="success"
                round
                @click="openOrder(row.itemKey.data.id)"
              ></VaButton>
              <!-- <VaButton
                :icon="isExpanded ? 'va-arrow-up' : 'va-arrow-down'"
                preset="primary"
                @click="row.toggleRowDetails()"
              >
              </VaButton> -->
            </div>
          </template>

          <template #cell(Descripción)="{ ...value }">
            <p class="break-words text-balance">
              {{ value?.source }}
            </p>
          </template>

          <template #cell(Estado)="{ row }">
            <VaSelect
              v-model="row.itemKey.data.status"
              :background="
                row.itemKey.data.status === 4 ? '#c3ffcb' : row.itemKey.data.status === 5 ? '#ffa1a1' : '#d5e3ff'
              "
              :readonly="row.itemKey.data.status === 4"
              :options="statusOptions"
              value-by="id"
              text-by="label"
              placeholder="---"
              :loading="isOnChangeStatus === row.itemKey.data.id"
              class="text-xs"
              style="--va-input--border-color: transparent; width: 120px"
              @update:modelValue="onChangeStatus(row.itemKey.data)"
            />
          </template>

          <template #cell(Servicios)="{ ...value }">
            <div class="flex flex-col gap-1" style="--va-badge-text-wrapper-border-radius: 50%">
              <VaBadge v-for="i in value?.source" :key="i" overlap>
                <VaChip
                  size="small"
                  color="#d5e3ff"
                  class="text-xxs font-bold px-1"
                  style="--va-chip-sm-height: 1.25rem"
                >
                  {{ i.label }}
                </VaChip>
              </VaBadge>
            </div>
          </template>

          <template #expandableRow="{ rowData }">
            <div class="mx-10 my-2 px-2 py-3 gap-2 bg-blue-50">
              <div class="columns-4 w-full mb-4 bg-white p-2">
                <div>
                  <p class="font-bold">Cotización creada:</p>
                  <span>{{ rowData.createdAt }}</span>
                </div>

                <div>
                  <p class="font-bold">Última Actualización:</p>
                  <span>{{ rowData.updatedAt }}</span>
                </div>
              </div>

              <div class="bg-white p-2">
                <div class="columns-4 w-full mb-2">
                  <div class="w-full">
                    <p class="font-bold">Cotizado por:</p>
                    <span class="capitalize">{{ rowData.data?.owner?.name }}</span>
                  </div>

                  <div class="w-full">
                    <p class="font-bold">Dirigido a:</p>
                    <span>{{ rowData.Cliente }}</span>
                  </div>
                </div>
              </div>
            </div>
          </template>

          <template #bodyAppend>
            <tr>
              <td colspan="9">
                <div class="flex justify-end mt-4">
                  <VaPagination
                    v-model="currentPage"
                    size="small"
                    class="text-xs mb-3 justify-center sm:justify-start"
                    buttons-preset="primary"
                    active-page-color="textPrimary"
                    :pages="pages()"
                  />
                </div>
              </td>
            </tr>
          </template>
        </VaDataTable>
      </div>
    </div>

    <VaModal v-model="showConfirm" size="250px" hide-default-actions no-dismiss>
      <p class="text-sm">¿Deseas eliminar esta cotización?</p>
      <template #footer>
        <VaButton size="small" preset="secondary" class="mr-1" @click="showConfirm = null"> Cancelar</VaButton>
        <VaButton size="small" :loading="isDeleting" @click="doDelete"> Continuar</VaButton>
      </template>
    </VaModal>

    <VaModal v-model="showConfirmStatus" size="250px" hide-default-actions no-dismiss>
      <p class="text-sm">¿Deseas cambiar el estado de esta cotización?</p>
      <template #footer>
        <VaButton size="small" preset="secondary" class="mr-1" @click="cancelStatus()"> Cancelar</VaButton>
        <VaButton size="small" :loading="isOnChangeStatus" @click="doChangeStatus"> Continuar</VaButton>
      </template>
    </VaModal>

    <VaModal v-model="viewOrders" size="280px" hide-default-actions close-button>
      <p v-if="orders.length > 1" class="text-sm">Ordenes registradas:</p>
      <p v-if="orders.length == 1" class="text-sm">Orden registrada:</p>
      <ul class="list-decimal px-4 text-sm mt-3">
        <li v-for="order of orders" :key="order" class="mb-2">
          <span class="font-bold">000{{ order }}-24</span>
          <VaButton
            size="small"
            preset="primary"
            :to="'/orders/view/' + order"
            class="ml-1"
            style="text-transform: capitalize; top: -2px"
            @click="orderStore.hideOrders()"
          >
            Ver Orden
          </VaButton>
        </li>
      </ul>
    </VaModal>

    <VaModal v-model="showNewOportunity" hide-default-actions size="medium" close-button blur no-dismiss>
      <div class="mt-4">
        <NewOportunity @close="closeNew" />
      </div>
    </VaModal>

    <VaModal v-model="showEdit" hide-default-actions size="medium" close-button blur no-dismiss>
      <div class="mt-4">
        <NewOportunity :data="showEdit" :detail="onlyPreview" @close="closeNew" />
      </div>
    </VaModal>
  </div>
</template>
